.icon-left {
    position: absolute;
    top: 10px;
    left: 10px;
}

.icon-right {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.season-display i {
    color: white;
}

.season-display {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
}

h1 {
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.winter {
    background-image: url(/static/media/ian-keefe-449093-unsplash.2a3b8c36.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.summer {
    background-image: url(/static/media/sean-o-406693-unsplash.f280043e.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

